@use "global" as *;

.p-mv {
  position: relative;
  height: 100vh;
}
.p-mv__img {
  width: 100%;
  height: 100vh;
}
.p-mv__img img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.p-mv__block {
  color: $white;
  writing-mode: vertical-rl;
  position: absolute;
  top: 33.4%;
  right: 25.4%;
  z-index: 2;
}

.p-mv__title {
  font-size: rem(20);
  letter-spacing: -0.039em;
  line-height: 2.4;
  @include mq("md") {
    font-size: rem(34);
  }
}
@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}

.swiper-slide-active .p-mv__img,
.swiper-slide-duplicate-active .p-mv__img,
.swiper-slide-prev .p-mv__img {
  animation: zoomUp 10s linear 0s;
  animation-fill-mode: both;
}
