@use "global" as *;

.p-sub-mv {
  height: rem(580);
  width: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}

.p-sub-mv__title {
  color: $white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: rem(34);
  text-align: center;
  font-weight: $extrabold;
  padding-top: rem(80);
}
