@use "global" as *;

.p-footer-nav__items {
  color: $black;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.p-footer-nav__item {
  padding: 0 rem(17);
}

// _::-webkit-full-page-media,
// _:future,
