_:lang(x) + _:-webkit-full-screen-document,
.p-footer-nav__item {
  padding: 0 rem(26);
}

.p-footer-nav__item a {
  line-height: 1;
  display: block;
  writing-mode: vertical-rl;
  font-size: rem(18);
  color: $black;
  letter-spacing: 0.8em;
}
